.search {
  width: 100%;
  max-width: 810px;
  margin: 0 auto;

  &__container {
    display: flex;
  }

  &__input {
   width: 100%;
    height: 40px;
    border: 2px solid #ffdb4d;
    border-right: 0;
    border-radius: 10px 0 0 10px;
    padding: 0 15px;
    outline: 0;
    box-sizing: border-box;
  }

  &__button {
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 20px;
    border: 2px solid #ffdb4d;
    border-left: 0;
    outline: 0;
    box-sizing: border-box;
    cursor: pointer;
    color: #000;
    background: #ffdb4d;
    font: 400 14px Roboto, sans-serif;
    text-decoration: none;
    border-radius: 0 10px 10px 0;
    transition: .5s;

    &:hover {
      background: #f7d036;
    }
  }
}