.ad {

  &__container {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;

    &::before {
      content: '';
      display: flex;
      padding-top: 100%;
    }
  }

  &__link {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    width: 100%;
    height: 100%;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: transparent;
      transition: .5s;
    }

    &:hover {
      &::after {
        background: rgba(0, 0, 0, 0.4);
      }
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}