.footer {
  background: #555;

  &__container {
    display: flex;
    justify-content: center;
    padding: 15px;
  }

  &__link {
    margin-right: 20px;
    cursor: pointer;
    color: #999;
    font: 400 14px Roboto, sans-serif;
    text-decoration: none;

    &:hover {
      color: #fff;
    }
  }

  &__copyright {
    color: #999;
    font: 400 14px Roboto, sans-serif;
  }
}