@import "fonts";
@import "./compoents/header";
@import "./compoents/footer";
@import "./compoents/search";
@import "./compoents/ad";
@import "./compoents/ads";
@import "./compoents/index";

body {
  margin: 0;
  padding: 0;
}

* {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.background {
  background: url("../img/background.jpg") no-repeat;
  background-size: cover;
}

.layout {
  .search {
    padding: 120px 0;
  }
}

.container {
  max-width: 1920px;
  margin: 0 40px;

  @media screen and (min-width: 2000px) {
    margin: 0 auto;
  }
}

.logo {

  &__text {
    font: 700 24px Roboto, sans-serif;
    color: #fff;
  }
}

.button {
  display: flex;
  align-items: center;
  height: 40px;
  padding: 0 20px;
  border: 1px solid #000;
  box-sizing: border-box;
  border-radius: 4px;
  outline: 0;
  cursor: pointer;
  color: #000;
  background: #fff;
  font: 400 14px Roboto, sans-serif;
  text-decoration: none;

  &--yellow {
    background: #ffdb4d;
  }
}